<template lang="html">

<div id="app">
    <view-header></view-header>
    <router-view></router-view>
</div>
</template>


<script>
    window.nowTime = Math.floor(new Date().getTime()/1000);
    import { mapGetters } from "vuex"
    import Header from "@/components/header";
    export default {
        name: "app",
        mounted () {
        console.log(this.$router.name);
        
        },
        components: {
            "view-header":Header,
        },
        computed: {
        /*...mapGetters({
            openChat: 'getOpenChat', // 是否开启消息窗
        }),*/
        }
    };

</script>

<style lang="less">
@import './assets/css/variable.less';
@import './assets/css/public.less';
@import './assets/css/reset.css';
@import './assets/css/elReset.less';
#app {
  font-size: .9rem;
//   min-width: 1200px;
  font-family: arial,'Microsoft Yahei',\\5fae\8f6f\96c5\9ed1, PingFang SC !important;
  position: relative;
  overflow: hidden;
  height: 100%;
}
*{
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-tap-highlight-color: transparent;
}
#app .header{
    position: absolute;
    height: 3.5rem;
    background: #1e2543;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9;
}
#app .header ~ .content{
    top: 3.5rem;
}
#app .content{
    position: absolute;
    top: 3.5rem;
    left: 0;
    right: 0;
    bottom: 0;
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    z-index: 8;
    &::-webkit-scrollbar {
        width: 10px;
        background-color: transparent;
    }
    &::-webkit-scrollbar-track {
        background-color: #1e2543; 
    }
    &::-webkit-scrollbar-thumb {
        width: 10px;
        border-radius: 10px;
        background-color: #344178;
    }
}
.app-main {
  width: 1200px;
  margin: 0 auto;
  min-height: 800px;
}
// 滚动条自定义
::-webkit-scrollbar {
	width: 10px;
	background-color: transparent;
}
::-webkit-scrollbar-track {
	background-color: #f8f9fb; 
}
::-webkit-scrollbar-thumb {
	width: 10px;
	border-radius: 10px;
	background-color: #d5d5d5;
}
.fade-enter-active, .fade-leave-active {
  transition: all 0.3s ease;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
body{
   background: url(./assets/images/public/body.jpg) no-repeat top center #171b35;
   overflow: hidden;
}
@media only screen and (max-width: 1770px) {
    #app .content{
        overflow-x: auto;
    }
}
@media only screen and (max-width: 800px) {
  #app .header{
    height: 4rem;
  }
  #app .content{
    top: 4rem;
  }
  #app .content{
    &::-webkit-scrollbar {
      display: none;
    }
  }
}
</style>



