const  publics=[

    {
      path: '/demo',
      name: 'demo',
      component: resolve => require(['@/views/demo/index'], resolve),
      meta: {
        title: 'demo页',
        requireAuth: false,
        pageType: 'demo',
        appstyle:'default',
        headerShow:true,
        footerShow:true,
        keepalive:false
      }
    },
    {
      path: '/',
      name: 'index',
      component: resolve => require(['@/views/index/index'], resolve),
      meta: {
        title: '首页',
        requireAuth: false,
        pageType: 'index',
        appstyle:'default',
        headerShow:true,
        footerShow:true,
        keepalive:false
      }
    },


  ]

export { publics }



