import Vue from 'vue'
import store from '@/vuex/store'

//战队logo默认图片替换

Vue.filter('int', function(data) {
    var d = data; 
    return parseInt(d);
});
Vue.filter('langfmt', function(value,cnt) {
    return value.replace("%s",cnt);
});
Vue.filter('addreplace', function(value,strBegin,strEnd) {
      // var str = value.substr(0, [3]);
      //没有图片，则返回-1  
      //str.substr(0,parseInt(str.split('').length/2))+'**'+str.substr(parseInt(str.split('').length/2+2),str.split('').length)
      if(!strBegin)strBegin=2;
      if(!strEnd)strEnd=3;
      return value.substr(0, [strBegin])+"**"+value.substr(-strEnd, [strEnd]);
});
Vue.filter('addreplaces', function(value) {
       var str = value.substr(0, [3]);
      //没有图片，则返回-1  
      //str.substr(0,parseInt(str.split('').length/2))+'**'+str.substr(parseInt(str.split('').length/2+2),str.split('').length)
        return value.substr(0, [8])+"**"+value.substr(-8, [12]);  
});
Vue.filter('endtime', function(value) {
    // value是整数，否则要parseInt转换
function checkTime(i){   
       //将0-9的数字前面加上0，例1变为01     
       if(i<10)   {   
       i = "0" + i;    
       }     
       return i;   
} 
        var leftTime = (new Date(value)) - (new Date());        
        var days = parseInt(leftTime / 1000 / 60 / 60 / 24 , 10); //计算剩余的天数     
       var hours = parseInt(leftTime / 1000 / 60 / 60 % 24 , 10); //计算剩余的小时     
       var minutes = parseInt(leftTime / 1000 / 60 % 60, 10);//计算剩余的分钟     
       var seconds = parseInt(leftTime / 1000 % 60, 10);//计算剩余的秒数     
       days = checkTime(days);     
       hours = checkTime(hours);     
       minutes = checkTime(minutes);     
       seconds = checkTime(seconds);     
        var timehtml= hours+":" + minutes+":"+seconds;   
        return timehtml; 
    var time = new Date((parseInt(value)- new Date()+28800)*1000);
    var h = time.getHours();
    var m = time.getMinutes();
    return leftTime;
    //return '剩' +(h<10?'0'+h:h) + '小时' + (m<10?'0'+m:m)+'分钟';
});
Vue.filter('time', function(value) {
 	// value是整数，否则要parseInt转换
    var time = new Date((parseInt(value)+28800)*1000);
    var y = time.getFullYear();
    var m = time.getMonth()+1;
    var d = time.getDate();

    return (y<10?'0'+y:y) + '-' + (m<10?'0'+m:m) + '-' + (d<10?'0'+d:d);
});
Vue.filter('mdhmtime', function(value) {
    // value是整数，否则要parseInt转换
    var time = new Date((parseInt(value))*1000);
    var y = time.getFullYear();
    var m = time.getMonth()+1;
    var d = time.getDate();
    var h = time.getHours();
    var mm = time.getMinutes();

    return (m<10?'0'+m:m) + '-' + (d<10?'0'+d:d)+"  "+(h<10?'0'+h:h) + ':' + (mm<10?'0'+mm:mm) ;
});
Vue.filter('fullTime', function(value) {
 	// value是整数，否则要parseInt转换
    var time = new Date((parseInt(value))*1000);
    var Y = time.getFullYear();
    var M = time.getMonth()+1;
    var D = time.getDate();
    var h = time.getHours();
    var m = time.getMinutes();
    var s = time.getSeconds();

    return Y + '-' + (M<10?'0'+M:M) + '-' + (D<10?'0'+D:D) + ' ' + (h<10?'0'+h:h) + ':' + (m<10?'0'+m:m) + ':' + (s<10?'0'+s:s);
});

Vue.filter('getDateDiff', function(dateTimeStamp) {
    var minute = 60;
    var hour = minute * 60;
    var day = hour * 24;
    var halfamonth = day * 15;
    var month = day * 30;

    var now = Date.parse(new Date())/1000;
    var diffValue = now - (parseInt(dateTimeStamp)+28800);
    if (diffValue < 0) {

        //若日期不符则弹出窗口告之

        //alert("结束日期不能小于开始日期！");

    }
    var monthC = diffValue / month;
    var weekC = diffValue / (7 * day);
    var dayC = diffValue / day;
    var hourC = diffValue / hour;
    var minC = diffValue / minute;
    let result;
    if (monthC >= 1) {
        result = "发表于" + parseInt(monthC) + "个月前";
    } else if (weekC >= 1) {
        result = "发表于" + parseInt(weekC) + "周前";
    } else if (dayC >= 1) {
        result = "发表于" + parseInt(dayC) + "天前";
    } else if (hourC >= 1) {
        result = "发表于" + parseInt(hourC) + "个小时前";
    } else if (minC >= 1) {
        result = "发表于" + parseInt(minC) + "分钟前";
    } else
        result = "刚刚发表";
    return result;
});

/**
 * 金额格式化 添加“,”分隔符
 * @param {Number} money 金额
 */
Vue.filter('formatMoney', function (money) {
    if (money == "") {
        return "";
    }
    // if (money) {
    //     money = money.trim();
    // }
    if (/[^0-9\.\-\+]/.test(money)) {
        return money;
    }
    money = parseFloat(money) + "";
    if ('NaN' == money) {
        return "0.00";
    }
    var money_flag = "";
    if (money.indexOf("-") != -1) {
        money = money.replace(/-/g, "");
        money_flag = "-";
    }

    money = money.replace(/^(\d*)$/, "$1.");
    money = (money + "00").replace(/(\d*\.\d\d)\d*/, "$1");
    money = money.replace(".", ",");
    var re = /(\d)(\d{3},)/;
    while (re.test(money)) {
        money = money.replace(re, "$1,$2");
    }
    money = money.replace(/,(\d\d)$/, ".$1");
    var money = money_flag + "" + money.replace(/^\./, "0.")
    return money;
});

// 全局自定义指令
// 滑动验证
Vue.directive('move', function (el) {
    var done = false; // 是否通过验证
    el.onmousedown = function (e) {
        var X = e.clientX - el.offsetLeft
        document.onmousemove = function (e) {
            var endx = e.clientX - X
            el.className = 'move moveBefore'
            el.style.left = endx + 'px'
            // console.log(el.parentNode.children[0])
            var width = $('.movebox').width() - $('.move').width()
            el.parentNode.children[0].style.width = endx + 'px'
            el.parentNode.children[1].innerHTML = '请按住滑块，拖动到最右边'
            //临界值小于
            if (endx <= 0) {
                el.style.left = 0 + 'px'
                el.parentNode.children[0].style.width = 0 + 'px'
                //$('.movego').width(0)
                store.dispatch('setMoveSuccess', false);
            }
            //临界值大于
            // console.log(el.style.left)
            if (parseInt(el.style.left) >= width) {
                el.style.left = width + 'px'
                el.parentNode.children[0].style.width = width + 'px'
                el.parentNode.children[1].className = 'txt success'
                el.parentNode.children[1].innerHTML = '验证通过'
                el.className = 'move moveSuccess'
                el.onmousedown = null;
                done = true;
                store.dispatch('setMoveSuccess', true);
            }
        }
    }
    document.onmouseup = function (e) {
        document.onmousemove = null;
        if (done) {
            store.dispatch('setMoveSuccess', true);
            return;
        }
        el.style.left = 0 + 'px'
        el.parentNode.children[0].style.width = 0 + 'px'
        store.dispatch('setMoveSuccess', false);
    }
})


