import axios from "axios";
import qs from "qs";
import { Message } from "element-ui";
import router from "../router";
import store from '@/vuex/store'
import { setCookie, getCookie, deleteCookie } from "../assets/js/cookieHandle";

//POST传参序列化(添加请求拦截器)
axios.interceptors.request.use(
  config => {
    // 在发送请求之前做某件事
    if (
      config.method === "post" ||
      config.method === "put" ||
      config.method === "delete"
    ) {
      // 序列化
      config.data = qs.stringify(config.data);
    }
    
    // 若是有做鉴权token , 就给头部带上token
    if (getCookie("token")) {
      config.headers.common['X-USER-TOKEN'] = getCookie("token");
    }
    
    return config;
  },
  error => {
    Message({
      //  饿了么的消息弹窗组件,类似toast
      showClose: true,
      message: error.data.errmsg,
      type: "error"
    });
    return Promise.reject(error.data.errmsg);
  }
);

//返回状态判断(添加响应拦截器)
axios.interceptors.response.use(
  res => {
    //对响应数据做些事
    if (res.data && res.data.errcode != 0) {
      if (res.data.errcode == 10006) { // 用户未登录或登录状态信息过期
        store.dispatch('setBaseLoginUserInfo', '');
        deleteCookie("token");
        // Message({
        //   message: res.data.errmsg,
        //   type: 'error'
        // });
        setTimeout(() => {
          router.push({
            path: "/login"
          });
        }, 3000);
      }
      else {
        Message({
          //  饿了么的消息弹窗组件,类似toast
          message: res.data.errmsg,
          type: "error"
        });
      }

      return Promise.reject(res.data);
    }
    return res.data;
  },
  error => {
    // 用户登录的时候会拿到一个基础信息,比如用户名,token,过期时间戳
    // 直接丢localStorage或者sessionStorage
    // if (!getCookie("token")) {
    //   // 若是接口访问的时候没有发现有鉴权的基础信息,直接返回登录页
    //   router.push({
    //     path: "/login"
    //   });
    // } else {
      // 若是有基础信息的情况下,判断时间戳和当前的时间,若是当前的时间大于服务器过期的时间
      // 乖乖的返回去登录页重新登录
      // let lifeTime =
      //   JSON.parse(window.localStorage.getItem("loginUserBaseInfo")).lifeTime *
      //   1000;
      // let nowTime = new Date().getTime(); // 当前时间的时间戳
      // console.log(nowTime, lifeTime);
      // console.log(nowTime > lifeTime);
      // if (nowTime > lifeTime) {
      //   Message({
      //     showClose: true,
      //     message: "登录状态信息过期,请重新登录",
      //     type: "error"
      //   });
      //   router.push({
      //     path: "/login"
      //   });
      // } else {
        // 下面是接口回调的satus ,因为我做了一些错误页面,所以都会指向对应的报错页面
        if (error.response.status === 403) {
          router.push({
            path: "/error/403"
          });
        }
        if (error.response.status === 500) {
          // router.push({
          //   path: "/error/500"
          // });
        }
        if (error.response.status === 502) {
          router.push({
            path: "/error/502"
          });
        }
        if (error.response.status === 404) {
          // router.push({
          //   path: "/error/404"
          // });
        }
      // }
      
    // }
    console.log(error.response.status);


    // 返回 response 里的错误信息
    let errorInfo = error.data && error.data.errcode != 0 ? error.data.errmsg : error.data;
    return Promise.reject(errorInfo);
  }
);

// 对axios的实例重新封装成一个plugin ,方便 Vue.use(xxxx)
export default {
  install: function (Vue, Option) {
    Object.defineProperty(Vue.prototype, "$http", { value: axios });
  }
};


