Date.prototype.format = function(fmt)
{ //author: meizz
    var o = {
        "M+" : this.getMonth()+1,                 //月份
        "d+" : this.getDate(),                    //日
        "h+" : this.getHours(),                   //小时
        "m+" : this.getMinutes(),                 //分
        "s+" : this.getSeconds(),                 //秒
        "q+" : Math.floor((this.getMonth()+3)/3), //季度
        "S"  : this.getMilliseconds()             //毫秒
    };
    if(/(y+)/.test(fmt))
        fmt=fmt.replace(RegExp.$1, (this.getFullYear()+"").substr(4 - RegExp.$1.length));
    for(var k in o)
        if(new RegExp("("+ k +")").test(fmt))
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length==1) ? (o[k]) : (("00"+ o[k]).substr((""+ o[k]).length)));
    return fmt;
}

//init web3 for window.web3
var Web3 = require("web3");
var onewinnerabi = require("@/abi/onewinner");

if (typeof web3 !== 'undefined') {
    window.web3 = new Web3(web3.currentProvider);
    window.onewinnerAddress = '0x4a7fbdcd272a97cf32bbf2892f6e52687faa7169';
    //初始化相关的合约
    window.onewinnerContract = new web3.eth.Contract(onewinnerabi,onewinnerAddress);
    web3.eth.getAccounts(function(err,accounts) {
        if (err != null || accounts.length == 0) {
            //console.log("小狐狸还没有登录，锁住中");
            MessageBox.confirm(i18n.t("m.public.address_not_found"), i18n.t("m.public.tip"), {
                  confirmButtonText: i18n.t("m.public.confirm"),
                  showClose:false,
                  showCancelButton:false,
                  type: 'warning'
            }).then(() => {
                // 确定
              location.reload();
            }).catch(() => {    
              location.reload();   
            });
        }
        else {
            window.eth_account = accounts[0];
            web3.eth.net.getId().then(function(netid){
                window.netId = netid;
                if(netId!=net_id){
                    MessageBox.confirm(i18n.t("m.public.net_work_err"), i18n.t("m.public.tip"), {
                        confirmButtonText: i18n.t("m.public.confirm"),
                        showClose:false,
                        showCancelButton:false,
                        type: 'warning'
                    }).then(() => {
                        // 确定
                        location.reload();
                    }).catch(() => {
                        location.reload();
                    });
                }
                initVue();
            });
        }
    });
} else {
    //console.log("小狐狸没安装");
    MessageBox.confirm(i18n.t("m.public.dapp_not_support"), i18n.t("m.public.tip"), {
          confirmButtonText: i18n.t("m.public.confirm"),
          showClose:false,
          showCancelButton:false,
          type: 'warning'
    }).then(() => {
        // 确定
      location.reload();
    }).catch(() => {    
      location.reload();   
    });
    
    initVue();
}
