<template lang="html">
    <div class="header">
        <div class="header-con wrap flex-box">
            <div class="logo">
                <img src="@/assets/images/header/logo.png" alt="">
            </div>
            <div class="header-tab flex-1 flex-box">
                <a v-bind:class="'tab-list '+((menu_name=='onewinner')?'active':'') " v-bind:href="menu_url" v-for="(menu_url,menu_name) in this.menu_config">{{$t('m.public.menu.'+menu_name)}}</a>
            </div>
            <div class="header-rule flex-box">
                <div class="rule-titie">{{$t('m.public.menu.lang_switch')}}</div>
                <div class="select-rule" @click="open_lang()">
                    <img src="@/assets/images/header/flag.png" alt="" v-if="langname=='简体中文'">
                    <img src="@/assets/images/header/flaghk.png" alt="" v-if="langname=='繁体中文'">
                    <img src="@/assets/images/header/English.png" alt="" v-if="langname=='English'">
                    <div class="iconfont">&#xe622;</div>
                    <div class="lang" :class="{'active':is_lang}">
                        <div class="lang-list english" @click="changeLang('en-us')"><img src="@/assets/images/header/English.png" alt="">English</div>
                        <div class="lang-list chinese" @click="changeLang('zh-cn')"><img src="@/assets/images/header/flag.png" alt="">简体</div>
                        <div class="lang-list chinese" @click="changeLang('zh-hk')"><img src="@/assets/images/header/flaghk.png" alt="">繁体</div>
                    </div>
                </div>
            </div>
            <div class="header-menu" @click="menu()">
                <div class="iconfont">&#xe629;</div>
            </div>
            <div class="header-nav" :class="{'active':is_menu}">
                <a :href="menu_url" class="h-nav-list" v-for="(menu_url,menu_name) in this.menu_config">{{$t('m.public.menu.'+menu_name)}}</a>
            </div>
        </div>
    </div>
</template>
<script>
    import { setCookie, getCookie } from '@/assets/js/cookieHandle'
    export default {
        components: {

        },
        data () {
            return {
                is_lang:false,
                langname:'',
                is_menu:false,
                menu_config:menu_config
            }
        },
        created(){
            this.$i18n.locale = localStorage.getItem('lang') || 'en-us';
            switch (localStorage.getItem('lang')) {
                case 'zh-cn':
                    this.langname = '简体中文';
                    break;
                case 'zh-hk':
                    this.langname = '繁体中文';
                    break;
                case 'en-us':
                    this.langname = 'English';
                    break;
                default:
                    this.langname = 'English';
                    break;
            }
        },
        mounted () {

        },
        methods:{
            /**
             * 切换语言
             */
            changeLang(lang) {
                console.log(123);
                this.$i18n.locale = lang;
                setCookie('_lang', lang);
                localStorage.setItem('lang', lang);
                switch (lang) {
                    case 'zh-cn':
                        this.langname = '简体中文';
                        break;
                    case 'zh-hk':
                        this.langname = '繁体中文';
                        break;
                    case 'en-us':
                        this.langname = 'English';
                        break;

                    default:
                        break;
                }

            },
            /**
             * 打开/关闭语言弹窗
             */
            open_lang(){
                this.is_menu = false;
                if (this.is_lang) {
                    this.is_lang = false;
                }else{
                    this.is_lang = true;
                }
            },
            /**
             * 打开/关闭菜单
             */
            menu(){
                this.is_lang = false;
                if (this.is_menu) {
                    this.is_menu = false;
                }else{
                    this.is_menu = true;
                }
            }
        },
    }
</script>

<style lang="less">
    .header-con {
        height: 100%;
        img{
            display: block;
        }
        .logo {
            width: 11rem;
            flex-shrink: 0;
            img{
                width: 6.75rem;
            }
        }
        .header-tab {
            height: 3.5rem;
            line-height: 3.5rem;
            .tab-list {
                margin: 0 1.75rem;
                position: relative;
                cursor: pointer;
                color:#fff;
            }
            .tab-list.active{
                &::after{
                    display: block;
                    content: '';
                    background: #ee1b59;
                    width: 100%;
                    height: 0.15rem;
                    bottom: 0;
                    left: 0;
                    position: absolute;
                    border-radius: 0.15rem;
                }
            }
        }
        .header-rule{
            flex-shrink: 0;
            color:#fff;
        }
        .rule-titie {
            margin-right: 2rem;

        }
        .select-rule {
            padding-right: 1rem;
            position: relative;
            img {
                width: 1.25rem;
            }
            .lang{
                position: absolute;
                top: 2.2rem;
                background: #fff;
                border-radius: 5px;

                height: 0;
                color: #333;
                right: 0;
                font-family: 'Microsoft Yahei';
                overflow: hidden;
                width: 7rem;
                transition: all 0.3s;
                .lang-list {
                    height: 2rem;
                    line-height: 2rem;
                    font-size: 0.9rem;
                    position: relative;
                    padding-left: 2.8rem;
                    padding-right: 1rem;
                    cursor: pointer;
                    img {
                        position: absolute;
                        left: 1rem;
                        top: 50%;
                        transform: translateY(-50%);
                    }
                }
            }
            .lang.active{
                height: 7rem;
                padding: 0.6rem 0;
            }
            .iconfont{
                position: absolute;
                display: block;
                content: '';
                top: 50%;
                right: 0;
                transform: translateY(-50%);
                font-size: 0.35rem;
            }
        }
        .header-menu{
            display: none;
        }
        .header-nav {
            position: absolute;
            top: 100%;
            background: #181c38;
            left: 0;
            right: 0;
            line-height: 3.35rem;
            padding: 0 1rem;
            overflow: hidden;
            height: 0;
            transition: all 0.3s;
            .h-nav-list {
                display: block;
                color: #fff;
                cursor: pointer;
                border-bottom: 1px solid #3d4879;
                &:last-child{
                    border-color: transparent;
                }
            }
        }
    }
    @media only screen and (max-width: 1770px) {
        .header-con{
            padding: 0 1rem;
        }
    }
    @media only screen and (max-width: 1200px) {
        .header-con{
            width: 100%;
            padding: 0 1rem;
        }
    }
    @media only screen and (max-width: 800px) {
        #app .header{
            height: 4rem;
            .header-con{
                height: 4rem;
                line-height: 4rem;
                .logo{
                    flex: 1;
                    img{
                        width: 4.83333rem;
                    }
                }
                .select-rule{
                    img{
                        width: 1.4rem;
                    }
                }
                .header-tab,.rule-titie{
                    display: none;
                }
                .header-menu{
                    display: block;
                    margin-left: 1.6rem;
                    .iconfont{
                        font-size: 1.2rem;
                    }
                }
                .header-nav.active{
                    height: 6.8rem;
                }
            }
        }
    }
</style>


